.App {
    text-align: center;
}

.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 80px;
}

.App-header {
    background-color: #222;
    height: 150px;
    padding: 20px;
    color: white;
}

.App-title {
    font-size: 1.5em;
}

.App-intro {
    font-size: large;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.loader {
    font-size: 42px;
    font-family: Google Sans, sans-serif;
    font-weight: bold;
    top: 50%;
    position: absolute;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%);
}

.imageHolder {
    margin-top: 10%;

}

.comic-details {
    margin-top: 20px;
    padding: 10px 10px;
    font-family: "Lora", sans-serif;
    font-size: 17px;
}

h1:before {
    content: "Loading...ยง";
    position: absolute;
    overflow: hidden;
    max-width: 7em;
    white-space: nowrap;
    color: #006064;
    -webkit-animation: loading 8s linear;
            animation: loading 8s linear;
}

@-webkit-keyframes loading {
    0% {
        max-width: 0;
    }
}

@keyframes loading {
    0% {
        max-width: 0;
    }
}

.wordText {
    font-size: 42px;
    font-family: "Mehr Nastaliq Web", sans-serif;
}

.wordMeaning {
    font-size: 24px;
    font-family: "Google Sans", sans-serif;
    font-weight: 100;
}

.quoteTextHolder {
    padding: 20px 10px;
    padding-bottom: 1px;
    -webkit-box-shadow: 0px 0px 10px 0px #a2a2a2;
            box-shadow: 0px 0px 10px 0px #a2a2a2;
}



.misra {
    font-family: "Lora", sans-serif;
    font-size: 18px;
    color: black;
    font-weight: bold;
}

.misra-translation {
    margin-bottom: 35px;
    color: #a5a5a5;
    font-size: 14px;
}

.links{
    margin-top: 10px;
}




